import { useState } from 'react';
import { useRouter } from 'next/router';
import Head from 'next/head';

export default function AddAnime() {
  const router = useRouter();
  const [form, setForm] = useState({ title: '', genre: '' });

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await api.addAnime(form);
      router.push('/');
    } catch (error) {
      console.error('Erro ao cadastrar:', error);
      alert('Erro ao cadastrar anime!');
    }
  };

  return (
    <>
      <Head>
        <title>Adicionar Anime</title>
      </Head>

      <div className="min-h-screen bg-[#F8F9FA] p-6">
        <div className="max-w-md mx-auto bg-white p-6 rounded-lg shadow-md">
          <h1 className="text-2xl font-bold text-[#EE0000] mb-4">Adicionar Anime</h1>
          <form onSubmit={handleSubmit}>
            <div className="mb-4">
              <label className="block text-gray-700 mb-2">Título</label>
              <input
                type="text"
                value={form.title}
                onChange={(e) => setForm({ ...form, title: e.target.value })}
                className="w-full px-3 py-2 border rounded"
                required
              />
            </div>
            <div className="mb-4">
              <label className="block text-gray-700 mb-2">Gênero</label>
              <input
                type="text"
                value={form.genre}
                onChange={(e) => setForm({ ...form, genre: e.target.value })}
                className="w-full px-3 py-2 border rounded"
                required
              />
            </div>
            <button
              type="submit"
              className="w-full bg-[#EE0000] text-white py-2 rounded hover:bg-red-700"
            >
              Salvar
            </button>
          </form>
        </div>
      </div>
    </>
  );
}